import Loading from '~/assets/js/loading';

export default {
    loading: false,
    data: function () {
        return {
            is_loading: true,
            is_anim_start: false, // ローディング開始後の背景透過用
        };
    },
    methods: {
        // 初期化処理
        init: function () {
            let loading = new Loading();
            loading.init();

            setTimeout(() => {
                // 開始1秒後に背景透過
                this.is_anim_start = true;
            }, 1000);

            loading.elm.addEventListener('end-anim', (evt) => {
                this.is_loading = false;
            });
        },
    },
    mounted: function () {
        this.init();
    },
};
